"use client";
import {
  convertAmountFromOnChainToHumanReadable,
  isUserTransactionResponse,
} from "@aptos-labs/ts-sdk";
import { truncateAddress } from "@aptos-labs/wallet-adapter-react";
import { TO_THE_MOON_ADDRESS } from "@moon/config/env";
import { ActionType } from "@moon/helpers/enums";
import { IFAActivities, IToken, ITradingInfo } from "@moon/helpers/interfaces";
import { aptosClient } from "@moon/helpers/utils";
import panora from "@moon/services/panora";
import {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from "react";

export type TokenContextProp = {
  tokenInfo: IToken;
  tradingInfo: ITradingInfo[];
};

const TokenContext = createContext({} as TokenContextProp);

export const TokenProvider = ({ children }: { children: ReactNode }) => {
  const [tokenInfo, setTokenInfo] = useState<IToken>({} as IToken);
  const [tradingInfo, setTradingInfo] = useState<ITradingInfo[]>([]);
  const aptos = aptosClient();

  const getPrice = async () => {
    const response = await panora.get<IToken[]>("/prices", {
      params: {
        tokenAddress: TO_THE_MOON_ADDRESS,
      },
    });

    const data = response.data?.[0];

    setTokenInfo(data);
  };

  const getTradingInfo = async () => {
    const activities = await aptos.queryIndexer<IFAActivities>({
      query: {
        query: `query MyQuery {
          fungible_asset_activities(
            where: {owner_address: {_eq: "0x7d4bf74e429bd0a7a4ffe35f7008f3e533c758d088835fcc7f112e469bb1ee56"}}
            limit: 20
            order_by: {transaction_timestamp: desc}
          ) {
            amount
            asset_type
            transaction_version
            transaction_timestamp
            type
          }
        }`,
      },
    });

    console.log("Trading info:", activities);

    const tradingData = await Promise.all(
      activities.fungible_asset_activities.map(async (activity) => {
        const tx = await aptos.getTransactionByVersion({
          ledgerVersion: activity.transaction_version,
        });

        if (!isUserTransactionResponse(tx)) return null;

        return {
          amount: convertAmountFromOnChainToHumanReadable(activity.amount, 8),
          assetType: activity.asset_type,
          sender: truncateAddress(tx.sender),
          createdAt: new Date(activity.transaction_timestamp).toLocaleString(),
          type: activity.type,
          from: "",
          assetFrom: "",
          to: "",
          assetTo: "",
        };
      })
    );

    const tradingReduced = tradingData.reduce((acc, curr) => {
      const index = acc.findIndex((a) => a.createdAt === curr?.createdAt);

      if (index === -1) {
        acc.push(curr as ITradingInfo);
      } else {
        if (curr?.type === ActionType.Deposit) {
          acc[index].from = curr?.amount.toFixed(3) as string;
          acc[index].assetFrom = curr?.assetType as string;
          acc[index].to = acc[index].amount.toFixed(3);
          acc[index].assetTo = acc[index].assetType;
        } else {
          acc[index].to = curr?.amount.toFixed(3) as string;
          acc[index].assetTo = curr?.assetType as string;
          acc[index].from = acc[index].amount.toFixed(3);
          acc[index].assetFrom = acc[index].assetType;
        }
      }

      return acc;
    }, [] as ITradingInfo[]);

    setTradingInfo(tradingReduced);
  };

  useEffect(() => {
    getPrice();
    getTradingInfo();

    const interval = setInterval(() => {
      getPrice();
      getTradingInfo();
    }, 1000 * 60 * 10); // Update every 10 minutes

    return () => clearInterval(interval);
  }, []);

  return (
    <TokenContext.Provider value={{ tokenInfo, tradingInfo }}>
      {children}
    </TokenContext.Provider>
  );
};

export const useTokenInfo = () => {
  return useContext(TokenContext);
};
